import React, {useState} from "react";


function CreateNote(props){

    const [rows,changeRows]=useState("50px")

    const [buttonPressed,changeBorder]=useState("#4E9F3D")
    


    function handleSubmit(req){
        req.preventDefault();
        console.log(req.target[0].value)
        console.log(req.target[1].value)
        console.log(buttonPressed)
         let priority  = 0
        if (buttonPressed === "#FF5151")
        {
            priority  = 3
        }else if (buttonPressed === "#cb9d06"){
            priority  = 2
        }else {
            priority = 1
        }

        console.log(props)

fetch('https://localhost:8000', {
  
  method: 'POST',
  headers: {
    crossDomain:true,
    Accept: 'application/json',
    'Content-Type': 'application/json'
  },
  body: JSON.stringify({
    username : props.userName,
    email : props.email,
    Title: req.target[0].value,
    Content: req.target[1].value,
    Priority : priority
  })
});


    }


    return(
        <div className="createNote">
        <form onSubmit={handleSubmit}>
        <input className="form-control NoteTitle" onClick={()=>changeRows("200px")} style={{borderColor:buttonPressed, fontSize:"20px"}} type="text" placeHolder="Title"/>
        <textarea className="form-control NoteContent" onClick={()=>changeRows("200px")} style={{height:rows, borderColor:buttonPressed, fontSize:"16px"}} type="text" placeHolder="Content"/>
        <div className="Notebutton">
        <button className="circle-button" style={{backgroundColor:"#FF5151"}} onClick={()=>changeBorder("#FF5151")} type="button"></button>
        <button className="circle-button" style={{backgroundColor:"#FBF46D"}} onClick={()=>changeBorder("#cb9d06")} type="button"></button>
        <button className="circle-button" style={{backgroundColor:"#4E9F3D"}} onClick={()=>changeBorder("#4E9F3D")} type="button"></button>
        <button type="submit" className="btn btn-success">Add</button>
        </div>
        </form>
        </div>
    );
}

export default CreateNote