import React, {useState} from "react"
import firebase from 'firebase/compat/app';
import 'firebase/compat/auth';
import {getAuth, signInWithPopup, GoogleAuthProvider } from "firebase/auth";


const app = firebase.initializeApp({
  apiKey: "AIzaSyDpxvPsr_V7Ts7YEnkzyNhJz9VOwII5e88",

  authDomain: "notesapp-ce328.firebaseapp.com",

  projectId: "notesapp-ce328",

  storageBucket: "notesapp-ce328.appspot.com",

  messagingSenderId: "670556433888",

  appId: "1:670556433888:web:f39d973801a2bb32924f85",

  measurementId: "G-0NP36Q2CDG"
})

const auth=firebase.auth()


function Header(props){  


const [loggedIn, setloggedIn]=useState(false);
const [loginButton, setloginButton]=useState("Login");
const [userName, setUserName]=useState("");
const [Email, setEmail]=useState("");


const handleLogin = () => {

if (loggedIn===false){
const provider = new firebase.auth.GoogleAuthProvider();
signInWithPopup(auth, provider)
.then((res)=>{
  setUserName(res.user.displayName)
  setEmail(res.user.email)
  setloginButton("Logout")
  setloggedIn(true)
  props.InfoProvider(res.user.displayName, res.user.email, true)
  
})
.catch((err)=>{
  console.log(err)
});
}else{
  setloggedIn(false)
  setloginButton("Login")
  setUserName("")
  setEmail("")
  props.InfoProvider("", "", false)

}
};


    return(
        <header>
<nav className="navbar navbar-light bg-dark justify-content-between">
  <a className="navbar-brand"><h2 style={{color:"white"}}>Simple Notes</h2></a>

  <p className="login-text">{userName}</p> <button className="btn btn btn-success my-2 my-sm-0 login-button" type="submit" onClick={handleLogin}>{loginButton}</button>

</nav>
</header>)
}
export default Header